import React, { useState } from 'react';
import PropTypes from 'prop-types';
import ClipboardJS from 'clipboard';
import styled from 'styled-components';

const Links = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  max-width: 600px;
  border-radius: 10px;
  font-size: 1.8rem;
  margin: 0 auto;
  margin-top: 5rem;
  padding: 4rem 2.5rem;
  min-height: 220px;
  background-color: #fff;
  box-shadow: 0 5px 10px rgba(154, 160, 185, 0.5),
    0 15px 40px rgba(166, 173, 201, 0.2);
  .short {
    font-weight: bold;
    font-size: 2.4rem;
    margin-bottom: 1rem;
    word-break: break-all;
  }
  .url {
    text-align: center;
  }
  .copy {
    background-color: #dceefb;
    color: #4098d7;
    /* font-weight: bold; */
    padding: 0.5rem 1rem;
    font-size: 1.8rem;
    border-radius: 100px;
    height: 50px;
    outline: none;
    border: none;
    cursor: pointer;
    width: 150px;
    transition: all 0.2s;
    margin-top: 2rem;
    &:hover {
      background-color: #4098d7;
      color: #fff;
    }
  }
  .copied,
  .copied:hover {
    background-color: #57ae5b;
    color: #fff;
  }
`;

// eslint-disable-next-line no-new
new ClipboardJS('.copy');

const UrlShortened = ({ response: { originalUrl, shortUrl } }) => {
  const [copied, setCopied] = useState(false);
  const handleClick = () => {
    setCopied(true);
    setTimeout(() => {
      setCopied(false);
    }, 1500);
  };

  return (
    <Links>
      <div>
        <p className="url short">{shortUrl}</p>
        <p className="url">{originalUrl}</p>
      </div>
      <button
        type="button"
        className={`copy ${copied && 'copied'}`}
        data-clipboard-text={shortUrl}
        onClick={handleClick}
      >
        {copied ? 'Copied!' : 'Copy'}
      </button>
    </Links>
  );
};

UrlShortened.propTypes = {
  response: PropTypes.object,
  originalUrl: PropTypes.string,
  shortUrl: PropTypes.string,
};

export default UrlShortened;
